<h1 id="capacitor" tabindex="-1"><a class="header-anchor" href="#capacitor" aria-hidden="true">#</a> Capacitor</h1>
<h2 id="projet-existant" tabindex="-1"><a class="header-anchor" href="#projet-existant" aria-hidden="true">#</a> Projet existant</h2>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>  ionic integrations enable capacitor
  ionic build
  npx cap add android
  npx cap open android
  npx cap copy
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h2 id="installer-google-map" tabindex="-1"><a class="header-anchor" href="#installer-google-map" aria-hidden="true">#</a> Installer google map</h2>
